    getColor(attributes) {
      if (attributes == "") return "red";
      else if (attributes == "") return "blue";
      else if (attributes == "") return "green";
      else if (attributes == "") return "purple";
      else if (attributes == "") return "yellow";
      else return "gray";
    },
    // Check if array contains all elements of another array
    checker(arr, target) {
      return target.every((v) => arr.includes(v));
    },
    // (,)

    searchKeysValues(lists, filters) {
      let key = Object.keys(filters);
      if (key.length == 0) {
